@import "index.css";

.layout__root {
    display: grid;
    grid-template-areas:
        "header header"
        "sidebar content";
    grid-template-rows: auto 1fr;
    background-color: #222222;
    height: 100vh;
    grid-template-columns: 300px 1fr;
}

.layout__header {
    grid-area: header;
    background-color: #2b2b2b;
    height: 50px;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
}

.layout__sidebar {
    grid-area: sidebar;
    width: 300px;
    color: white;
    display: flex;
    flex-direction: column;
    padding: 30px;
}

.layout__feed, .layout__friends, .layout__myPage, .layout__logo {
    cursor: pointer;
}

.layout__feed, .layout__friends {
    margin-top: 15px;
}

.layout__content {
    grid-area: content;
    align-self: center;
}

.layout__menu {
    display: flex;
}

.layout__menu-logout {
    padding: 4px;
    border-radius: 5px;
    cursor: pointer;
}

.layout__menu-avatar {
    height: 35px;
    width: 35px;
    border-radius: 50%;
    margin-right: 15px;
}

.layout__menu-logout:hover {
    background-color: #cecbcb;
}